
// colors

$test:              #314552;
$test_layer:        #2b3c48;
$test_elements:     #e6b875;
$mission_gradient:     linear-gradient(90deg,#000 0%,#98d5ff 100%);
$about_gradient:     linear-gradient(90deg,#000 0%,#4f7085 100%);


* {
  outline: none !important;
}
body {
  background: $test;
  font-family: HelveticaRegular;
  
  &.remove-scroll {
    overflow: hidden !important;
  }
}

// STYLE
#preloader {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: $test;
  z-index: 20;
  visibility: visible;
  opacity: 1;
  overflow: hidden;
  transition: all .5s ease;

  .loader-text {
    overflow: hidden;
    position: relative;
    width: 100%;
    height: 100%;
    color: $test_elements;
    justify-content: center;
    align-items: center;
    flex-flow: column;
    display: flex;
    line-height: 10rem;
    font-size: 10rem;
    letter-spacing: 50px;
    font-family: BebasNeueThin;

    .pride {
      pointer-events: none;
      position: relative;
      opacity: 1;
      //width: 50%;
      animation: loader 1s ease;
      transition: all .5s ease;
    }
    .club {
      pointer-events: none;
      position: relative;
      opacity: 1;
      animation: loader 1s ease;
      transition: all .5s ease;
    }
  }
  &.ok {
    transform: scale(0, 1);
    transform-origin: center;
    opacity: 0 !important;
    visibility: hidden !important;
  }
}

.btn-default {

  a, button {
    cursor: pointer;
    background: transparent;
    border: 1px solid $test_elements;
    border-radius: 25rem;
    position: relative;
    display: flex;
    align-items: center;
    padding: .7rem;
    justify-content: space-around;
    padding-left: 35% !important;
    min-width: 200px;
    color: $test_elements;
    text-transform: uppercase;
    text-decoration: none;
    font-size: .8rem;
    letter-spacing: 2px;
    font-family: CalibriLight;
    transition: all .5s ease;

    svg {
      height: 18px;
      fill: $test_elements;
      width: 18px;
      transform: rotate(0);
      transition: all .5s ease;
    }

    &:after {
      content: '';
      position: absolute;
      left: -55%;
      display: flex;
      height: 1px;
      width: 80%;
      background: $test_elements;
      pointer-events: none;
      transform: scale(1, 1);
      transform-origin: right;
      transition: all .5s ease;
    }

    &:hover {
      background: $test_elements;
      color: #fff;
      transition: all .5s ease;

      svg {
        transform: rotate(360deg);
        fill: #fff;
        transition: all .5s ease;
      }

      &:after {
        transform: scale(.4, 1);
        background: #fff;
        transition: all .5s ease;
      }
    }
  }
}

#header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 40px;
  z-index: 10;
  padding: 0;
  margin: 2rem 5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;

  .logo a {
    display: flex;
    flex-flow: column;
    font-size: 1.5rem;
    color: #fff;
    text-transform: uppercase;
    line-height: 23px;
    text-decoration: none;
    font-family: HelveticaBold;
    z-index: 15;
    position: relative;
  }
  .group {
    display: flex;
    justify-content: space-between;
    align-items: center;
    //width: 75%;
  }
  ul {
    display: flex;
    align-items: center;
    list-style: none;
    margin: 0;
    padding: 0;

    li {

      a {
        display: block;
        padding: 1rem 2rem;
        color: rgba(255,255,255, .6);
        text-decoration: none;
        font-family: HelveticaLight;
        text-transform: uppercase;
        transition: all .5s ease;

        span {
          position: relative;
          letter-spacing: 1px;
          font-size: 12px;
        }

        & span:after {
          content: '';
          position: absolute;
          //bottom: 7px;
          bottom: 0;
          //left: 5%;
          left: 0;
          width: 100%;
          height: 1px;
          opacity: 0;
          visibility: hidden;
          background: $test_elements;
          //transform: scale(0, 1);
          transform-origin: left;
          pointer-events: none;
          transition: all .5s ease;
        }
      }
      &:hover {
        a {
          color: rgba(255,255,255, 1);
          transition: all .5s ease;

          & span:after {
            bottom: -20px;
            //transform: scale(1, 1);
            opacity: 1;
            visibility: visible;
            transition: all .5s ease;
          }
        }
      }
      &.active {
        a {
          color: rgba(255,255,255, 1);
          transition: all .5s ease;

          & span:after {
            bottom: -20px;
            //transform: scale(1, 1);
            opacity: 1;
            visibility: visible;
            transition: all .5s ease;
          }
        }
      }
    }
  }

  .btn-default {
    margin-left: 10rem;

    a {
      justify-content: flex-end;
      padding: .7rem;
      min-width: 300px;

      &:after {
        left: -40%;
        width: 60%;
      }
      svg {
        width: 20px;
        height: 20px;
      }
    }
  }
  .btn-mobile {
    display: none;
    position: relative;
    flex-flow: column;
    align-items: flex-end;
    width: 40px;
    height: 30px;
    margin-left: 2rem;
    z-index: 15;
    justify-content: space-around;
    transition: all .5s ease;

    span {
      height: 4px;
      background: #fff;
      width: 100%;
      transition: all .5s ease;

      &:first-child {
        transform: translateX(.5rem);
      }
      &:nth-child(2) {
        opacity: 1;
        transform: translateX(-.5rem);
      }
      &:last-child {
        transform: translateX(.5rem);
      }
    }
    &:hover {
      transition: all .2s ease;

      span {
        transform: translateX(0);
        transition: all .2s ease;
      }
    }


    &.close {
      opacity: 1;
      transition: all .5s ease;

      span {
        border-radius: 25rem;

        &:first-child {
          width: 30px;
          transform: translateX(-5px) translateY(4px) rotate(40deg);
          transition: transform .5s ease,  width .5s ease .2s;
        }
        &:nth-child(2) {

          width: 60px;
          transform: translateX(-.5rem) translateY(3px);
          transition: transform .5s ease,  width .5s ease .2s;
        }
        &:last-child {
          width: 30px;
          transform: translateX(-5px) translateY(2px)  rotate(-40deg);
          transition: transform .5s ease,  width .5s ease .2s;
        }
      }
    }
  }
  .mobile-group {
    display: none;
    //display: flex;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: $test;
    z-index: 14;
    visibility: hidden;
    opacity: 0;
    overflow: hidden;
    transform: scale(.5);
    transition: all .5s ease;

    &.show {
      visibility: visible;
      opacity: 1;
      transform: scale(1);
      transition: all .2s ease;
    }


    #menu-mobile {
      width: 100%;
      height: 100%;
      justify-content: center;
      flex-flow: column;

      li {
        margin: 0;
        a {
          span {
            font-family: BebasNeueThin;
            letter-spacing: 10px;
            font-size: 3rem;
          }
        }
        & a:hover span:after {
          bottom: -10px;
        }
        &.active a span:after {
          bottom: -10px;
        }
      }
    }
  }
}
.section {
  position: relative;
  color: #fff;
  padding: 40px 0 !important;

  .container-fluid {
    padding: 0 5rem;
  }
}
#footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 40px;
  z-index: 9;
  padding: 0;
  margin: 2rem 5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;

  .scroll {
    position: relative;
    display: flex;
    align-items: center;
    color: #fff;
    font-family: BebasNeueThin;
    font-size: 26px;
    letter-spacing: 5px;
    text-decoration: none;
    opacity: .5;
    transition: all .5s ease;

    &.go_home {
      svg {
        transform: rotate(-90deg) !important;
      }
    }
    svg {
      width: 13px;
      height: 13px;
      fill: #fff;
      transition: all .5s ease;
    }

    &:after {
      content: '';
      position: absolute;
      bottom: 7px;
      right: -11px;
      width: 35px;
      height: 25px;
      border-radius: 25rem;
      border: 1px solid #fff;
      z-index: 1;
    }

    &:hover {
      opacity: 1;
    }
  }

  .footer-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 600px;

    .for-arrow svg {
      display: none;
    }

    .number-page {
      font-family: BebasNeueThin;
      font-size: 2rem;
      width: 30px;
      text-align: center;
      color: $test_elements;
    }
    span {
      height: 1px;
      background: #fff;
      opacity: .5;
      width: 300px;
    }

    a {
      opacity: .5;
      transition: all .5s ease;

      svg {
        width: 20px;
        height: 20px;
        fill: #fff;
        transform: scale(1);
        transition: all .5s ease;
      }

      &:hover {
        opacity: 1;
        transition: all .5s ease;

        svg {
          fill: $test_elements;
          transform: scale(1.2);
          transition: all .5s ease;
        }
      }
    }
  }
}

// STYLE SECTIONS
.section-mission {

  .fp-bg {
    top: 0;
    bottom: 0;
    width: 100%;
    position: absolute;
    z-index: -1;
    background: url(../img/bg-mission.png) no-repeat 0 -30px;
    background-size: cover;
  }

  .mission-content {
    display: flex;
    flex-flow: column;
    align-items: flex-end;

    h2 {
      position: relative;
      display: flex;
      flex-flow: column;
      align-items: flex-end;
      font-size: 9rem;
      letter-spacing: 35px;
      margin-right: -35px;
      margin-bottom: 3rem;

      &:after {
        content: '';
        position: absolute;
        bottom: -1rem;
        right: -10px;
        width: 50%;
        height: 10px;
        background: $test_elements;
      }

      span:first-child {
        font-family: BebasNeueBook;
        line-height: 6rem;
      }
      span:last-child {
        font-family: BebasNeueThin;
        color: $test_elements;

      }
    }

    .mission-text {
      max-width: 600px;

      p {
        line-height: 30px;
        text-align: right;
        font-family: CalibriLight;

        &:first-child {
          font-family: CalibriBold;
          text-transform: uppercase;
        }
      }
    }
  }
  .mission-image {
    position: relative;
    max-width: 100%;
    img {
      position: relative;
      box-shadow: 0 .5rem 100px rgba(0,0,0, .8);
      z-index: 1;
    }
    .under-image {
      left: 0;
      font-family: HelveticaLight;
      display: flex;
      justify-content: space-between;
      z-index: 0;
      margin-top: -9rem;

      span {
        position: relative;
        top: 3rem;
        opacity: .2;
        font-size: 9rem;
        text-transform: uppercase;

        &:first-child {
          margin-left: -10px;
        }
        &:last-child {
          margin-right: -10px;
        }
      }
    }
  }
}

.section-about {

  .fp-bg {
    top: 0;
    bottom: 0;
    width: 100%;
    position: absolute;
    z-index: -1;
    background: url(../img/bg-about.png) no-repeat 0;
    background-size: cover;

    &:after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      //width: calc(100% - 60%);
      width: 750px;
      height: 100%;
      background: $test_layer;
      z-index: 0;
    }
  }
  .about-content {
    position: relative;
    display: flex;
    align-items: flex-start;
    flex-flow: column;


    h2 {
      position: relative;
      display: flex;
      font-size: 8rem;
      margin-bottom: 5rem;
      font-family: BebasNeueBook;

      span {
        line-height: 7rem;
      }

      &:after {
        content: '';
        position: absolute;
        bottom: -3rem;
        left: 0;
        width: 50%;
        height: 10px;
        background: $test_elements;
      }
    }
    .about-text {
      line-height: 30px;
      text-align: left;
      font-family: CalibriLight;
      max-width: 600px;
      font-size: .9rem;

      p {
        span {
          font-family: CalibriBold;
          text-transform: uppercase;
          display: block;
        }
      }
    }
    .btn-default {
      margin-left: 7rem;
    }

    .about-video {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 700px;
      height: auto;
      margin-bottom: 6rem;
      transition: all .5s ease;

      img {
        position: relative;
        box-shadow: 0 0.5rem 100px rgba(0,0,0,.8);
        z-index: 1;
        transition: all .5s ease;
      }

      &:after {
        content: '';
        position: absolute;
        top: 3rem;
        left: -4rem;
        width: 100%;
        height: 100%;
        display: block;
        background: $test_elements;
        transition: all .5s ease;
      }

      .btn-play {
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        //width: 100%;
        //height: 100%;
        background: rgba(0,0,0, .4);
        z-index: 1;
        transition: all .5s ease;

        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: 50%;
          width: 150px;
          height: 150px;
          display: block;
          border: 1px solid #fff;
          border-radius: 25rem;
          transform: translate(-50%, -50%);
        }

        svg {
          position: relative;
          top: 0;
          left: 5px;
          fill: #fff;
          width: 50px;
          height: 50px;
          transform: rotate(0deg);
          transform-origin: center;
          transition: all .5s ease;
        }
      }

      &:hover {

        img {
          box-shadow: 0 0 100px rgba(0,0,0,.8);
          transition: all .5s ease;
        }

        &:after {
          top: 1rem;
          left: -1rem;
          transition: all .5s ease;
        }

        .btn-play {
          background: rgba(0,0,0, .2);
          transition: all .5s ease;

          svg {
            transform: rotate(360deg);
            transition: all .5s ease;
          }
        }

      }


    }
  }
}

.section-product {
  .fp-bg {
    top: 0;
    bottom: 0;
    width: 100%;
    position: absolute;
    z-index: -1;
    background: url(../img/bg-product.png) no-repeat 0;
    background-size: cover;
  }

  .product-title {
    h2 {
