  .product-content {
    position: relative;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    cursor: pointer;

    &:after {
      content: '/';
      position: absolute;
      top: 2rem;
      right: -9rem;
      font-family: HelveticaLight;
      font-size: 15rem;
      color: #3b5262;
      text-shadow: 1px 1px 50px rgba(0,0,0,.5);
