        align-items: flex-start;
        letter-spacing: 3rem;
        height: 65%;
        font-family: BebasNeueBold;
        font-size: 2rem;
        color: $test_elements;

        & > span {
          position: relative;
          display: flex;
          align-items: center;
          justify-content: space-around;
        }
      }
      & .lines_4 {
        height: 80%;
        top: 20px;
      }
    }

    p {
      line-height: 30px;
      text-align: left;
      font-family: CalibriLight;
      max-width: 600px;
      font-size: .9rem;

      span {
        font-family: CalibriBold;
        text-transform: uppercase;
        display: block;
      }
    }

    &:hover {
      h3 {
        text-shadow: 1px 1px 50px rgba(0, 0, 0, .5);
        transition: all .5s ease;
      }
    }
