.modal-custom {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: rgba(49, 69, 82, .98);
  z-index: 20;
  visibility: hidden;
  opacity: 0;
  overflow: hidden;
  transform: scale(.5);
  color: #fff;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  align-items: center;
  padding: 4rem;
  transition: all .5s ease;

  .modal-head {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 2rem;
    width: 100%;

    h2 {
      font-family: BebasNeueRegular;
      font-size: 100px;
      letter-spacing: 10px;
      text-overflow: ellipsis;
      overflow: hidden;

      &:after {
        content: '';
        position: absolute;
        bottom: -2rem;
        left: 20%;
        width: 25%;
        height: 10px;
        background: #e6b875;
      }
    }
    .btn-close {
      display: flex;
      position: absolute;
      right: 0;
      height: 100%;
      align-items: center;

      svg {
        fill: #fff;
        width: 40px;
        height: 40px;
        transform: rotate(180deg);
        opacity: .5;
        cursor: pointer;
        transition: all .5s ease;

        &:hover {
          opacity: 1;
          transform: rotate(-180deg);
          transition: all .5s ease;
        }
      }
    }
  }
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: -60%;
    width: 100%;
    height: 100%;
    display: flex;
    background: url(../img/product/first.png) no-repeat right;
    background-size: cover;
    z-index: -1;
  }
  .modal-container {
    position: relative;
    display: flex;
    flex-flow: row;
    justify-content: space-between;
    height: 100%;
    overflow: hidden;

    .modal-img {
      position: relative;
      //background: #fff;
      display: flex;
      width: 40%;
      height: 100%;
    }

  .modal-text {
    position: relative;
    width: 60%;
    padding-left: 3rem;
    padding-right: 3rem;
    margin-top: 3rem;
    line-height: 30px;
    font-family: CalibriLight;
    font-size: .9rem;
    overflow: auto;
    height: auto;

    &::-webkit-scrollbar {
      width: .2em;
    }

    &::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 6px rgba(255,255,255,0.2);
    }

    &::-webkit-scrollbar-thumb {
      background-color: #fff;
      opacity: .5;

    }

      p {
        span {
          font-family: CalibriBold;
          text-transform: uppercase;
          display: block;
        }
      }
    }
  }

  &.show {
    //display: block;
    visibility: visible;
    opacity: 1;
    transform: scale(1);
    transition: all .2s ease;
  }
}